<template>
  <div ref="content">
    <b-row class="mt-2 d-none d-md-none d-lg-flex mb-3">
      <b-col
        cols="12"
        xl="6"
        lg="6"
        sm="12"
        class="titleClass font-weight-bold mb-3"
        >{{ expenseItemName }}</b-col
      >
      <b-col cols="12" xl="12" lg="12" sm="12">
        <span class="d-flex flex-wrap expense-btn">
          <b-form-radio-group
            id="proposal-group-expense"
            @change="updateProposalList"
            v-model="selected_expense"
            :options="expenseOptions"
            name="proposal-group-expense"
            class="smallFontClass"
            size="sm"
          ></b-form-radio-group>
        </span>
      </b-col>
    </b-row>

    <b-row class="for-mobile mb-3">
      <b-col
        cols="12"
        xl="6"
        lg="6"
        class="titleClass font-weight-bold mb-3 text-center"
        >{{ expenseItemName }}</b-col
      >
      <b-col cols="12" xl="3" lg="3" sm="12" class="margin-bottom-1">
        <button
          @click.prevent="toggleSubtree = !toggleSubtree"
          class="w-100 px-3 py-2 dropdownToggle border-0 d-flex justify-content-between align-items-center"
        >
          <span class="menuHeader">{{ $t("SELECTEXPENSE") }}</span>
          <span
            ><i
              :class="toggleSubtree ? 'fa fa-caret-down' : 'fa fa-caret-down'"
            ></i
          ></span>
        </button>
        <b-row v-if="toggleSubtree" class="dropdownToggleBody no-gutters">
          <b-col
            id="expenseAccountDropdown"
            cols="12"
            class="checkboxExpenseItems scrollable-element"
          >
            <div class="submenuScroll dropdown-menu">
              <SubMenuTreeCheckbox
                :tree-options="treeOptions"
                @update-expense-type="getExpItmCodeArray"
                :treeTitle="''"
                :disabledItems="disabledExpenseItems"
                class="mt-1"
              />
            </div>
          </b-col>
        </b-row>
      </b-col>
    </b-row>
    <div v-if="pageloading" class="text-center">
      <i class="fa fa-spinner fa-spin fa-2x" />
    </div>
    <b-row v-else>
      <b-col cols="12" xl="12" lg="12" sm="12">
        <template v-if="messages.type_proposal.data.length == 0">
          <b-row>
            <b-col>{{ $t("NOPROPOSALEXISTS") }}</b-col>
          </b-row>
        </template>
        <template v-for="message in messages.type_proposal.data">
          <div :key="message.id" class="card-margin mb-3">
            <b-row
              v-if="message.notifications && message.notifications.length != 0"
              class="smallFontClass font-weight-bold mt-4 mb-2"
            >
              <b-col>{{ message.subtitle }}</b-col>
            </b-row>
            <div
              v-for="notification in message.notifications"
              :key="notification.id"
            >
              <b-row
                :class="{
                  'background-toggle': notification.id % 2 != 0,
                }"
                class="padding-level-2 center-items no-gutters bar-padding"
              >
                <b-col
                  cols="12"
                  xl="4"
                  lg="4"
                  sm="12"
                  class="mobile-text textHeader padding-level-1"
                  >{{ notification.label }}</b-col
                >
                <b-col
                  cols="12"
                  xl="4"
                  lg="4"
                  sm="12"
                  class="mobile-text textHeader"
                  >{{ notification.sublabel }}</b-col
                >
                <b-col cols="12" xl="4" lg="4" sm="12" class="mobile-text">
                  <b-button
                    @click="individualExpenseConfirmation(notification)"
                    class="file-selection-btn float-right"
                    variant="primary"
                    >{{ $t("QUOTEREQUEST") }}</b-button
                  >
                </b-col>
              </b-row>
            </div>
          </div>
        </template>
        <template v-if="userManagement != -1">
          <b-row>
            <b-col class="titleClass font-weight-bold">{{
              userManagement.title
            }}</b-col>
          </b-row>
          <b-row class="padding-level-1 no-gutters">
            <b-col>
              <div v-html="userManagement.notice" />
            </b-col>
          </b-row>
        </template>
      </b-col>
    </b-row>
    <b-row>
      <b-col class="text-center">
        <b-button
          :disabled="messages.type_proposal.data.length <= 0"
          @click="expenseConfirmation"
          class="file-selection-btn mt-3 mobile-width"
          variant="primary"
          >{{ $t("BULKQUOTEREQUEST") }}</b-button
        >
      </b-col>
    </b-row>
    <b-row class="mt-5 text-center">
      <b-col cols="12" class="d-flex align-items-center btn-end mblreturnBtn">
        <b-button
          @click="returnToExpenseEvaluationList"
          variant="dark"
          class="d-flex date-fs align-items-center justify-content-center returnBtn"
        >
          <i class="fa fa-caret-left fa-2x mr-2" aria-hidden="true"></i
          >{{ $t("RETURNTOLISTOFEXPENSEEVALUATION") }}</b-button
        >
      </b-col>
    </b-row>

    <b-modal
      id="proposal-complete-modal"
      ref="proposal-complete-modal"
      :hide-footer="true"
      centered
      variant="secondary"
      modal-class="py-0"
      body-class=" proposal-body"
      size="lg"
      footer-class="border-0"
      header-class="border-0 proposal-header"
      button-size="sm"
    >
      <template v-slot:modal-header="{ cancel }">
        <b-row class="w-100 d-flex align-items-center h-100 no-gutters">
          <b-col
            cols="12"
            xl="8"
            lg="8"
            sm="12"
            class="text-center mobile-margin-bottom"
          >
            <div class="title-fw float-left justify-content-initial">
              {{ $t("QUOTATIONSENDTO") }}
            </div>
          </b-col>
          <b-col cols="12" xl="4" lg="4" sm="12" class="text-center">
            <b-button
              @click="createQuotationRequest"
              :disabled="loading"
              class="send-btn float-right justify-content-end"
              variant="primary"
              >{{ $t("SEND") }}<i v-if="loading" class="fa fa-spinner fa-spin"
            /></b-button>
          </b-col>
        </b-row>

        <div class="w0">
          <button
            @click="cancel"
            type="button"
            aria-label="Close"
            class="close close-btn"
          >
            ×
          </button>
        </div>
      </template>
      <b-row>
        <b-col>
          <div class="modal-fs mb-2 font-weight-bold">
            {{ messages.type_proposal.title }}
          </div>
          <div class="mb-2">
            <b-row
              class="padding-level-2 background-toggle no-gutters center-items bar-padding"
            >
              <template>
                <b-col
                  :key="selectedNotification[0].id"
                  cols="12"
                  xl="4"
                  lg="4"
                  sm="12"
                  class="mobile-text textHeader padding-level-1"
                  >{{ selectedNotification[0].label }}</b-col
                >
                <b-col
                  :key="'A' + selectedNotification[0].id"
                  cols="12"
                  xl="8"
                  lg="8"
                  sm="12"
                  class="mobile-text textHeader"
                  >{{ selectedNotification[0].sublabel }}</b-col
                >
              </template>
            </b-row>
          </div>
        </b-col>
      </b-row>
      <b-row>
        <b-col cols="12" xl="3" lg="3" sm="12"></b-col>
        <b-col class="smallFontClass mb-2 errorMessage">
          <span>{{ sendMailError }}</span>
        </b-col>
      </b-row>
    </b-modal>

    <b-modal
      id="bulk-quotation-modal"
      ref="bulk-quotation-modal"
      :hide-footer="true"
      centered
      variant="secondary"
      modal-class="py-0"
      body-class="proposal-body"
      size="lg"
      footer-class="border-0"
      header-class="border-0 proposal-header"
      button-size="sm"
    >
      <template v-slot:modal-header="{ cancel }">
        <b-row class="h-100">
          <b-col cols="12" xl="8" lg="8" sm="12" class="text-center">
            <div class="title-fw float-left justify-content-initial">
              {{ $t("BULKPROPOSAL") }}
            </div>
            <div class="float-left mobile-margin-bottom f-class">
              {{ $t("BULKQUOTATIONFORALLCHECKED") }}
            </div>
          </b-col>
          <b-col cols="12" xl="4" lg="4" sm="12" class="text-center">
            <b-button
              :disabled="!isAnySelected"
              @click="bulkExpenseConfirmation"
              class="file-selection-btn float-right"
              variant="primary"
              >{{ $t("BATCHTRANSMISSION") }}</b-button
            >
          </b-col>
        </b-row>

        <div class="w0">
          <button
            @click="cancel"
            type="button"
            aria-label="Close"
            class="close close-btn"
          >
            ×
          </button>
        </div>
      </template>

      <b-row>
        <b-col>
          <div
            class="modal-fs font-weight-bold d-flex justify-content-intial mobileTitleClass"
          >
            {{ messages.type_proposal.title }}
          </div>
          <div class="mb-2">
            <b-row class="no-gutters">
              <template v-for="(message, index) in messages.type_proposal.data">
                <b-col :key="message.id" cols="12">
                  <div
                    v-if="
                      message.notifications && message.notifications.length != 0
                    "
                  >
                    <b-row class="font-weight-bold mt-4 mb-2">
                      <b-col>{{ message.subtitle }}</b-col>
                    </b-row>

                    <div
                      v-for="(notification, index1) in message.notifications"
                      :key="notification.id"
                    >
                      <b-row
                        :class="{
                          'background-toggle': notification.id % 2 != 0,
                        }"
                        class="padding-level-2 center-items no-gutters bar-padding"
                      >
                        <b-col
                          cols="12"
                          xl="1"
                          lg="1"
                          sm="12"
                          class="mobile-text padding-level-1"
                        >
                          <b-form-checkbox
                            @change="updateCheckAll"
                            :id="'checkbox-1' + index + index1"
                            v-model="notification.checked"
                            size="sm"
                          ></b-form-checkbox>
                        </b-col>
                        <b-col
                          cols="12"
                          xl="5"
                          lg="5"
                          sm="12"
                          class="mobile-text textHeader"
                        >
                          {{ notification.label }}
                        </b-col>
                        <b-col
                          cols="12"
                          xl="6"
                          lg="6"
                          sm="12"
                          class="mobile-text textHeader"
                          >{{ notification.sublabel }}</b-col
                        >
                      </b-row>
                    </div>
                  </div>
                </b-col>
              </template>
            </b-row>
          </div>
        </b-col>
      </b-row>
      <b-row>
        <b-col cols="12" xl="3" lg="3" sm="12"></b-col>
        <b-col class="smallFontClass mb-2 errorMessage">
          <span>{{ sendMailError }}</span>
        </b-col>
      </b-row>
    </b-modal>
    <b-modal
      id="bulk-proposal-complete-modal"
      ref="bulk-proposal-complete-modal"
      :hide-footer="true"
      centered
      variant="secondary"
      modal-class="py-0"
      body-class=" proposal-body"
      size="lg"
      footer-class="border-0"
      header-class="border-0 proposal-header"
      button-size="sm"
    >
      <template v-slot:modal-header="{ cancel }">
        <b-row class="w-100 d-flex align-items-center h-100 no-gutters">
          <b-col
            cols="12"
            xl="8"
            lg="8"
            sm="12"
            class="text-center mobile-margin-bottom"
          >
            <div class="title-fw float-left justify-content-initial">
              {{ $t("QUOTATIONSENDTO") }}
            </div>
          </b-col>
          <b-col cols="12" xl="4" lg="4" sm="12" class="text-center">
            <b-button
              @click="createBulkQuotationRequest"
              :disabled="loading"
              class="send-btn float-right justify-content-end"
              variant="primary"
              >{{ $t("SEND") }}<i v-if="loading" class="fa fa-spinner fa-spin"
            /></b-button>
          </b-col>
        </b-row>

        <div class="w0">
          <button
            @click="cancel"
            type="button"
            aria-label="Close"
            class="close close-btn"
          >
            ×
          </button>
        </div>
      </template>
      <b-row>
        <b-col>
          <div class="modal-fs mb-3 font-weight-bold">
            {{ messages.type_proposal.title }}
          </div>
          <div class="mb-2">
            <b-row
              v-for="message in messages.type_proposal.data"
              :key="message.id"
            >
              <b-col
                v-for="notification in message.notifications"
                :key="notification.id"
                cols="12"
              >
                <b-row
                  v-if="notification.checked"
                  :class="{
                    'background-toggle': notification.id % 2 != 0,
                  }"
                  class="padding-level-2 center-items no-gutters bar-padding"
                >
                  <b-col
                    v-if="notification.checked"
                    cols="12"
                    xl="3"
                    lg="3"
                    class="mobile-text padding-level-1"
                    >{{ notification.label }}</b-col
                  >
                  <b-col
                    v-if="notification.checked"
                    cols="12"
                    xl="9"
                    lg="9"
                    class="mobile-text padding-level-1"
                    >{{ notification.sublabel }}</b-col
                  >
                </b-row>
              </b-col>
            </b-row>
          </div>
        </b-col>
      </b-row>
      <b-row>
        <b-col cols="12" xl="3" lg="3" sm="12"></b-col>
        <b-col class="smallFontClass mb-2 errorMessage">
          <span>{{ sendMailError }}</span>
        </b-col>
      </b-row>
    </b-modal>

    <b-modal
      id="proposal-complete-confirmed-modal"
      ref="proposal-complete-confirmed-modal"
      :hide-footer="true"
      centered
      variant="secondary"
      modal-class="py-0"
      body-class="proposal-body py-0"
      size="lg"
      footer-class="border-0"
      header-class="border-0 pd-0"
      button-size="sm"
    >
      <template v-slot:modal-header="{ cancel }">
        <b-row> </b-row>

        <div class="w0">
          <button
            @click="cancel"
            type="button"
            aria-label="Close"
            class="close close-btn confirmation-close"
          >
            ×
          </button>
        </div>
      </template>
      <b-card class="pop-up-box">
        <b-row>
          <b-col>
            <div class="title-fw">{{ $t("QSENTTOSUPPLIER") }}</div>
            <div class="title-fw mb-4">{{ $t("WAITFORANSWER") }}</div>

            <div class="title-fw mb-4">
              <b-row
                v-for="notification in quotationNotification"
                :key="notification.id"
              >
                <template>
                  <b-col cols="12"
                    >{{ notification.label }} : {{ $t("QREQUESTNO") }} [{{
                      notification.qNo
                    }}]</b-col
                  >
                </template>
              </b-row>
            </div>
            <div class="title-fw">{{ $t("MENTIONQREQUESTNO") }}</div>
          </b-col>
        </b-row>
      </b-card>
    </b-modal>
  </div>
</template>

<script>
// import SubMenuTree from "@/components/SubMenuTree.vue";
import SubMenuTreeCheckbox from "@/components/SubMenuTreeCheckbox";
import { ApiServicesFactory } from "@/shared/apiServiceFactory";
const BasicInfoApis = ApiServicesFactory.get("basic");
import { mapGetters } from "vuex";
import { Settings } from "luxon";
Settings.defaultLocale = "ja";
import { endPoint } from "@/shared/endPoint";
import i18n from "@/plugins/i18n";

export default {
  name: "ReductionProposalPreview",
  components: { SubMenuTreeCheckbox },
  data() {
    return {
      selectExpenseItem: this.$t("EXPENSEITEMSELECTION"),
      messages: {
        type_proposal: {
          title: "",
          data: [],
        },
      },
      treeData: [],
      userManagement: {},
      expenseItemName: "",
      sendMailError: "",
      allTreeData: [],
      hostTreeData: [],
      loading: false,
      pageloading: false,
      quotationNotification: [
        {
          id: 1,
          label: "",
          qNo: "",
        },
      ],
      selectedNotification: [
        {
          id: 1,
          label: "",
          sublabel: "",
        },
      ],
      treeOptions: {
        checkbox: false,
      },
      selected_expense: {},
      expenseOptions: [],
      isAnySelected: true,
      toggleSubtree: false,
      disabledExpenseItems: [],
    };
  },
  computed: {
    ...mapGetters({
      selected_expense_item: "getSelectedExpenseItem",
      userDetails: "getUserDetails",
    }),
  },
  mounted() {
    this.getReductionProposalMessagesList();
  },
  created() {
    this.getTreeData();
  },
  methods: {
    getExpItmCodeArray(item) {
      this.toggleSubtree = false;
      console.log(this.allTreeData, "allTreeData");
      let treeItem = this._.find(this.allTreeData, { expense_item_nm: item });
      this.selected_expense = treeItem;
      console.log(treeItem);
      this.updateProposalList();
    },
    updateCheckAll() {
      setTimeout(() => {
        this.isAnySelected = false;
        this.messages.type_proposal.data.forEach((item) => {
          item.notifications.forEach((subitem) => {
            if (subitem.checked || this.isAnySelected) {
              this.isAnySelected = true;
              return;
            }
          });
        });
      }, 0);
    },
    returnToExpenseEvaluationList() {
      this.$router.push({ name: "expense_evaluation" });
    },
    displayButtons() {
      this.allTreeData.forEach((item) => {
        let iterateExpenseItem = {
          text: item.expense_item_nm ? item.expense_item_nm : "",
          value: {
            expense_item_cd: item.expense_item_cd ? item.expense_item_cd : "",
            accounts_name_cd: item.accounts_name_cd
              ? item.accounts_name_cd
              : "",
          },
        };
        if (item.expense_item_nm) {
          this.expenseOptions.push(iterateExpenseItem);
        }
      });
      if (this.selected_expense_item.expense_item_nm) {
        this.selected_expense = {
          accounts_name_cd: this.selected_expense_item.accounts_name_cd,
          expense_item_cd: this.selected_expense_item.expense_item_cd,
        };
      }
    },

    showExpenseConfirmationModal() {
      this.$refs["proposal-complete-modal"].show();
    },
    showBulkQuotationConfirmationModal() {
      this.$refs["bulk-proposal-complete-modal"].show();
    },
    showBulkQuotationModal() {
      this.$refs["bulk-quotation-modal"].show();
    },
    hideBulkQuotationModal() {
      this.$refs["bulk-quotation-modal"].hide();
    },
    hideExpenseConfirmationModal() {
      this.$refs["proposal-complete-modal"].hide();
    },
    hideBulkQuotationConfirmationModal() {
      this.$refs["bulk-proposal-complete-modal"].hide();
    },
    showExpenseCompletedConfirmationModal() {
      this.$refs["proposal-complete-confirmed-modal"].show();
    },
    hideExpenseCompletedConfirmationModal() {
      this.$refs["proposal-complete-confirmed-modal"].hide();
    },
    /**Function to pass the mail sending data to API and getting results for the completion process */
    async sendProposalMailData(receiverData) {
      const { data } = await BasicInfoApis.postAPI(
        endPoint.MESSAGEREPLY,
        receiverData
      );
      this.loading = false;
      this.sendMailError = "";
      if (data.success) {
        this.hideExpenseConfirmationModal();
        this.hideBulkQuotationConfirmationModal();
        this.showExpenseCompletedConfirmationModal();
        this.selectedNotification = [
          {
            id: 1,
            label: "",
            sublabel: "",
          },
        ];
      }
    },
    /** Function to update the tree store from the selected expense Item from side menu tree */
    updateProposalList() {
      setTimeout(() => {
        let treeItem = this._.find(this.allTreeData, {
          expense_item_cd: this.selected_expense.expense_item_cd,
        });
        this.$store.dispatch("updateSelectedExpenseItem", {
          accounts_name_cd: treeItem.accounts_name_cd,
          expense_item_cd: treeItem.expense_item_cd,
          expense_item_nm: treeItem.expense_item_nm,
        });
        this.getReductionProposalMessagesList();
      }, 0);
    },
    /**Function to compile the mail sender, receiver data and initiation for the process of mail sending */
    async sendProposalMessage() {
      let selectedSupplier = this.selectedNotification;
      let reductionProposalNumbers = [];
      selectedSupplier.forEach((item) => {
        reductionProposalNumbers.push(item.reduction_proposal_no);
      });
      let messageAddressArray = [];
      const { data } = await BasicInfoApis.postAPI(
        endPoint.MESSAGEREADINITIALPROPOSAL,
        {
          reduction_proposal_no: reductionProposalNumbers,
        }
      );
      if (data.success) {
        let responseData = data.data;
        responseData.forEach((item) => {
          let mailList = {
            message_destination_account_type: item.message_sender_account_type,
            message_destination_account_no: item.message_sender_account_no,
            message_destination_person_name: item.message_sender_person_name,
            message_sender_belongs: this.userDetails.clientCompanyName,
            message_sender_account_type: item.message_destination_account_type,
            message_sender_account_no: item.message_destination_account_no,
            message_sender_person_name: item.message_destination_person_name,
            message_title:
              this.$t("MESSAGEREPLYSUBJECT") + item.reduction_proposal_name,
            message_text:
              this.userDetails.clientPersonName +
              this.$t("MESSAGEREPLYCLIENTCONTEXT") +
              item.reduction_proposal_name +
              this.$t("MESSAGEREPLYCLIENTCONTEXTFOLLOW"),
            reduction_proposal_no: item.reduction_proposal_no,
            reduction_proposal_url: item.reduction_proposal_url,
            message_sender_icon_path: this.userDetails.icon_path
              ? this.userDetails.icon_path
              : "",
            attached_file_flg: false,
            del_flg: false,
            create_id: localStorage.getItem("client_account_no"),
            update_id: localStorage.getItem("client_account_no"),
            message_source: "request_quotation",
          };
          messageAddressArray.push(mailList);
        });
        this.sendProposalMailData(messageAddressArray);
      } else {
        this.loading = false;
        this.sendMailError = this.$t("SENDMAILERROR");
      }
    },
    /**function to route bulk quotation creation screen */
    expenseConfirmation() {
      this.sendMailError = "";
      this.showBulkQuotationModal();

      // this.$router.push({ name: "expense_details_confirmation" });
    },
    async createBulkQuotationRequest() {
      this.loading = true;
      this.sendMailError = "";
      let serviceChecked = [];
      this.messages.type_proposal.data.forEach((item) => {
        for (let i = 0; i < item.notifications.length; i++) {
          if (item.notifications[i].checked) {
            let proposalData = item.notifications[i];
            serviceChecked.push(proposalData);
          }
        }
      });
      this.selectedNotification = serviceChecked;
      let quotationData = [];
      serviceChecked.forEach((item) => {
        let serviceData = {
          reduction_proposal_no: item.reduction_proposal_no,
          supplier_no: item.supplier_no,
          person_id: item.person_id,
          client_no: localStorage.getItem("client_account_no"),
          client_name: item.client_name,
          expense_item_nm: this.selected_expense_item.expense_item_nm,
          del_flg: false,
          create_id: localStorage.getItem("client_account_no"),
          update_id: localStorage.getItem("client_account_no"),
        };
        quotationData.push(serviceData);
      });
      const { data } = await BasicInfoApis.postAPI(
        endPoint.EVALUATIONCREATEQUOTATION,
        quotationData
      );
      if (data.success) {
        let constructQuotationNotification = data.data;
        let generateId = 1;
        let insertedQuotation = [];
        constructQuotationNotification.forEach((item) => {
          let responseList = {
            id: generateId,
            label: item.supplier_name,
            qNo: item.request_quotation_no,
          };
          generateId++;
          insertedQuotation.push(responseList);
        });
        this.quotationNotification = insertedQuotation;
        this.sendProposalMessage();
      } else {
        this.loading = false;
        this.sendMailError = this.$t("MAILERRORCONTACTADMIN");
      }
    },

    /**Function to send data for creation quotationRequest  */
    async createQuotationRequest() {
      this.loading = true;
      let notificationIteration = this.selectedNotification;
      let quotationData = [];
      notificationIteration.forEach((item) => {
        let serviceData = {
          reduction_proposal_no: item.reduction_proposal_no,
          supplier_no: item.supplier_no,
          person_id: item.person_id,
          client_no: localStorage.getItem("client_account_no"),
          client_name: item.client_name,
          expense_item_nm: this.selected_expense_item.expense_item_nm,
          del_flg: false,
          create_id: localStorage.getItem("client_account_no"),
          update_id: localStorage.getItem("client_account_no"),
        };
        quotationData.push(serviceData);
      });
      const { data } = await BasicInfoApis.postAPI(
        endPoint.EVALUATIONCREATEQUOTATION,
        quotationData
      );
      if (data.success) {
        let constructQuotationNotification = data.data;
        let generateId = 1;
        let insertedQuotation = [];
        constructQuotationNotification.forEach((item) => {
          let responseList = {
            id: generateId,
            label: item.supplier_name,
            qNo: item.request_quotation_no,
          };
          generateId++;
          insertedQuotation.push(responseList);
        });
        this.quotationNotification = insertedQuotation;
        this.sendProposalMessage();
      } else {
        this.loading = false;
        this.sendMailError = this.$t("MAILERRORCONTACTADMIN");
      }
    },
    /**function to initiate for creation of request for particular selected proposal */
    async individualExpenseConfirmation(notification) {
      this.sendMailError = "";
      this.selectedNotification = [notification];
      this.showExpenseConfirmationModal();
    },
    /**function to initiate for creation of request for bulk selected proposal */
    async bulkExpenseConfirmation() {
      this.sendMailError = "";
      this.hideBulkQuotationModal();
      this.showBulkQuotationConfirmationModal();
    },
    /**Fetch user management data for selected expense Item and displaying it */
    async getUserManagementData() {
      const { data } = await BasicInfoApis.postAPI(
        endPoint.EVALUATIONUSERMANAGEMENT,
        {
          accounts_name_cd: this.selected_expense_item.accounts_name_cd,
          expense_item_cd: this.selected_expense_item.expense_item_cd,
        }
      );
      let responseData = data.data;
      if (responseData != undefined) {
        this.userManagement = {
          title: responseData.user_management_title,
          notice: responseData.user_management,
        };
      } else {
        this.userManagement = -1;
      }
    },
    /**Function to fetch and update the sidemenu expenseItem tree to store */
    async getTreeData() {
      const { data } = await BasicInfoApis.postAPI(
        endPoint.EVALUATIONREDUCTIONPROPOSALTREE,
        {
          client_no: localStorage.getItem("client_account_no"),
        }
      );
      if (data.success) {
        let responseData = data.data;
        this.allTreeData = responseData;
        let mapData = [];
        let groupedData = this._.groupBy(responseData, "accounts_name_nm");
        Object.keys(groupedData).forEach((key) => {
          let childrenArray = [];
          groupedData[key].forEach((item) => {
            let bindChildData = {
              text: item.expense_item_nm,
            };
            childrenArray.push(bindChildData);
          });
          let bindTreeData = {
            text: key,
            state: { expanded: true },
            children: childrenArray,
          };
          mapData.push(bindTreeData);
        });
        this.treeData = mapData;
        this.$store.dispatch("updateTree", mapData);
        this.displayButtons();
      }
    },
    /**Function used to fetch reductionProposalList for particular expenseItem and compile based on UI */
    async getReductionProposalMessagesList() {
      this.pageloading = true;
      const { data } = await BasicInfoApis.postAPI(
        endPoint.EVALUATIONREADREDUCTIONPROPOSALCLIENTEXPENSE,
        {
          client_no: localStorage.getItem("client_account_no"),
          accounts_name_cd: this.selected_expense_item.accounts_name_cd,
          expense_item_cd: this.selected_expense_item.expense_item_cd,
        }
      );
      this.pageloading = false;
      let responseData = data.data;
      let type1Data = [];
      let type2Data = [];
      let dataArray = [];
      let type1ID = 1;
      let type2ID = 1;
      this.expenseItemName =
        this.selected_expense_item.expense_item_nm + this.$t("REDUCTIONPLAN");
      if (responseData.length > 0) {
        responseData.forEach((item) => {
          if (item.reduction_proposal_type == "001") {
            let automaticData = {
              id: type1ID,
              label: item.supplier_name,
              sublabel: item.reduction_proposal_name,
              supplier_no: item.supplier_no,
              reduction_proposal_no: item.reduction_proposal_no,
              client_name: item.client_name,
              person_name: item.person_name,
              person_id: item.person_id,
              checked: true,
            };
            type1ID++;
            type1Data.push(automaticData);
          } else {
            let supplierData = {
              id: type2ID,
              label: item.supplier_name,
              sublabel: item.reduction_proposal_name,
              supplier_no: item.supplier_no,
              reduction_proposal_no: item.reduction_proposal_no,
              client_name: item.client_name,
              person_name: item.person_name,
              person_id: item.person_id,
              checked: true,
            };
            type2ID++;
            type2Data.push(supplierData);
          }
        });
        let dataSeparation = {
          id: 1,
          subtitle: i18n.t("SYSTEMAUTOMATICPROPOSAL"),
          notifications: type1Data,
        };
        dataArray.push(dataSeparation);
        dataSeparation = {
          id: 2,
          subtitle: i18n.t("SUPPLIERPROPOSAL"),
          notifications: type2Data,
        };
        dataArray.push(dataSeparation);
        this.messages.type_proposal.data = dataArray;
        this.messages.type_proposal.title =
          this.selected_expense_item.expense_item_nm + this.$t("REDUCTIONPLAN");
      } else {
        this.messages.type_proposal.data = [];
      }

      this.getUserManagementData();
    },
  },
};
</script>

