<template>
  <div class="min-tree-height">
    <div class="font-weight-bold">{{ treeTitle }}</div>
    <template v-if="treeData.length == 0">
      <div class="text-center errorColor">
        {{ $t("GENRALDATAERRORMESSAGE") }}
      </div>
    </template>
    <template v-else>
      <tree
        id="checkbox-group-tree"
        ref="tree"
        :data="treeData"
        :options="treeOptions"
        @node:selected="onNodeSelected"
        @node:checked="getCheckedItem"
        @node:unchecked="getCheckedItem"
      >
        <span slot-scope="{ node }" class="tree-text">
          <template v-if="!node.hasChildren()">
            <span
              :class="
                selected_expense_item.expense_item_nm == node.text
                  ? 'selected-tree-text'
                  : ''
              "
              class="tree-text mr-2"
            >
              <span :disabled="disabled(node.text)" 
                    :class="{
                      disabled: disabled(node.text),
                      'selected-tree-text': selected_expense_item.expense_item_nm == node.text
                }" class="children-text" > {{ node.text }} </span>
            </span>
          </template>

          <template v-else>
            <span :disabled="disabled(node.text)" class="parent-text"> {{ node.text }} </span>
          </template>
        </span>
      </tree>
    </template>
  </div>
</template>

<script>
import { mapGetters } from "vuex";
export default {
  name: "SubMenuTree",
  components: {},
  inheritAttrs: false,
  props: {
    treeOptions: {
      type: [Array, Object, String, Function],
      default: () => [],
    },
    treeTitle: {
      type: String,
      default: () => [],
    },
    disabledItems: {
      type: [Array],
      default: () => [],
    },
  },
  data: () => {
    return {
      ids: [],
      checkedItems: [],
    };
  },
  computed: {
    eventsList() {
      return this.events.concat().reverse();
    },
    ...mapGetters({
      costInfoTree: "getCostInfoTreeList",
      treeData: "getTree",
      selected_expense_item: "getSelectedExpenseItem",
      checkedItemsExpCodeArray: "getCheckedExpItemCodeArray",
    }),
  },
  mounted() {},
  methods: {
    getCheckedItem() {
      let checkedItems = this.$refs.tree
        .findAll({ state: { checked: true } })
        .map((item) => {
          return this._.find(this.costInfoTree, {
            expense_item_nm: item.text ? item.text : "",
          });
        });
         // checkedItems = checkedItems
      //   .filter((item) => item != null)
      //   .map((item) => item.expense_item_cd);
      let checkedDataItems = [];
      if(checkedItems.length > 0){
          checkedItems.forEach(item => {
          if(item != null){
            checkedDataItems.push({
              accounts_name_cd : item.accounts_name_cd,
              expense_item_cd : item.expense_item_cd
            })
          }
        });
      }
      this.$emit("update-multiselect-exp-item", checkedDataItems);
    },
    disabled(text) {
      return this.disabledItems.indexOf(text) == -1 ? false : true;
    },
    onNodeSelected(item) {
      let disableCheck = this.disabledItems.indexOf(item.text) == -1 && item.parent ? true : false;
       if (disableCheck) {
         this.$emit("update-expense-type", item.text);
         this.selectedExpenseItem = item.text;
       }
    },
    dd() {
      let allchecked = this.$refs.tree.findAll({
        state: { checked: false },
      });
      return allchecked;
    },
  },
};
</script>
<style scoped lang="scss">
$primary: #062854 !default;

.selected-tree-text {
  color: blue;
}
.disabled {
  opacity: 0.6;
}
.tree-text,
.link-text {
  font-size: 0.7rem;
  font-weight: 800;
  color: $primary;
}
</style>
<style lang="scss" >
.tree-content {
  padding-top: 0px !important;
  padding-bottom: 0px !important;
}
.tree-checkbox.checked {
  opacity: 1;
  background-image: url("data:image/svg+xml,%3csvg xmlns='http://www.w3.org/2000/svg' viewBox='0 0 8 8'%3e%3cpath fill='%23fff' d='M6.564.75l-3.59 3.612-1.538-1.55L0 4.26 2.974 7.25 8 2.193z'/%3e%3c/svg%3e") !important;
  background: no-repeat 50%/50% 50%;
}
.tree-checkbox.checked,
.tree-checkbox.indeterminate {
  background-color: #033170 !important;
}
.tree-checkbox {
  border-color: #000 !important;
  height: 23px !important;
  width: 23px !important;
}
.tree-arrow.has-child:after {
  border: none !important;
  display: none !important;
}
</style>
